<template>
  <div class="content">
    <article class="home" id="home">
      <div class="home__content">
        <HeaderPr
          :opened="opened" @toggleMenu="toggleMenu"
          @gotoPlace="gotoPlace"
        />
        <MobileMenu
          :show="opened" @toggleMenu="toggleMenu"
          @gotoPlace="gotoPlace"
        />
        <div class="home__content--head" v-if="texts">
          <div></div>
          <div class="home__content--head-group">
            <h1 class="home__content--title" v-html="texts[this.$store.state.lang].title"></h1>
            <p class="home__content--txt" v-html="texts[this.$store.state.lang].text"></p>
            <div
              @click="openWhats(texts.fone)"
              class="home__content--btn">{{texts[this.$store.state.lang].btn}}</div>
          </div>
          <!-- <img src="@/assets/seta.svg" alt="" class="home__content--img"> -->
          <div></div>
        </div>
        <div class="home__content--dizem"></div>
      </div>
    </article>
    <ReviewsPr/>
    <PrCastro/>
    <PrServices/>
    <ImoveisPr/>
    <ContatosPr/>
  </div>
</template>

<script>
import HeaderPr from '@/components/HeaderPr.vue'
import MobileMenu from '@/components/MobileMenu.vue'
import ReviewsPr from '@/components/ReviewsPr.vue'
import PrCastro from '@/components/PrCastro.vue'
import PrServices from '@/components/PrServices.vue'
import ImoveisPr from '@/components/ImoveisPr.vue'
import ContatosPr from '@/components/ContatosPr.vue'

export default {
  name: 'HomeView',
  components: {
    HeaderPr,
    MobileMenu,
    ReviewsPr,
    PrCastro,
    PrServices,
    ImoveisPr,
    ContatosPr,
  },
  beforeMount() {
    this.texts = this.$store.state.data.home;
    this.lang = this.$store.state.lang;
    console.log('this.$route', this.$route.params.place)
    if(this.$route.params.place) {
      this.gotoPlace(this.$route.params.place)
    }
  },
  data() {
    return {
      opened: false,
      texts: null,
      lang: null
    }
  },
  methods: {
    toggleMenu(opened) {
      this.opened = opened;
      if(opened) {  
        window.scrollTo({
          top: 0,
          behavior: 'smooth'
        });
      }
    },
    openWhats(fone) {
      window.open(`https://api.WhatsApp.com/send?phone=${fone}`, '_blank');
    },
    gotoPlace(op) {
      setTimeout(() => {
        const el = document.querySelector('#'+op)
        if (el) {
          if(this.$route.params.place !== op) {
            this.$router.replace(`/${op}`);
          }
          el.scrollIntoView({ behavior: "smooth" });
        }
      }, 0);
    }
  }

}
</script>
