<template>
  <div class="btn" v-if="showBtnUp" @click="goUp()">
    <img src="@/assets/seta.svg" alt="cima" class="btn--img">
  </div>
</template>
<script>
export default {
  data() {
    return {
      showBtnUp: false
    }
  },
  mounted() {
    this.onScroll();
  },
  methods: {
    onScroll() {
      window.onscroll = () => {
        if(window.pageYOffset > 100) { this.showBtnUp = true; }
        else if(window.pageYOffset < 100) { this.showBtnUp = false; }
      }
    },
    goUp() {
      if(this.$route.params.place) this.$router.replace('/');
      window.scrollTo({
        top: 0,
        behavior: 'smooth'
      });
    }
  }
}
</script>

<style scoped lang="scss">
@import '@/styles/global.scss';
.btn {
  &--img {
    width: 40px;
    transform: rotate(-180deg);
  }
  @include setFont($poppins, white, 80px);
